import React from "react";
import { graphql } from "gatsby";
import { initializeApp } from "firebase/app";
import { getFirestore } from "firebase/firestore/lite";
import Layout from "@components/layout";
import Seo from "@components/seo";
import BreadCrumb from "@components/common/breadcrumb";
import Web3DevelopmentScreen from "@screens/web3-development";
import { useLocation } from "@reach/router";

const Web3SmartContractDevelopment = (props) => {
  const app = initializeApp(props?.data?.site?.siteMetadata?.firestore);
  const db = getFirestore(app);
  const breadcrumbLinks = [
    { text: "Home", link: "/" },
    { text: "Web3 Smart Contract Development Company", link: null },
  ];
  return (
    <Layout db={db}>
      <BreadCrumb items={breadcrumbLinks} />
      <main>
        <Web3DevelopmentScreen db={db} />
      </main>
    </Layout>
  );
};

export const Head = (props) => {
  const { data } = props;
  const fileUrl =
    data?.web3SmartContractImg?.childImageSharp?.gatsbyImageData?.images
      ?.fallback?.src;
  const location = useLocation();
  return (
    <Seo
      title="Web3 Smart Contract Development Company | Justtry Technologies"
      metaTitle="Web3 Smart Contract Development Company | Justtry Technologies"
      description="Justtry Tech is a web3 smart contract development company that provides DeFi smart contract development services such as Binance smart contract development, Tron smart contract development, EOS smart contract development, and etc."
      image={fileUrl}
      siteFirestore={data?.site?.siteMetadata?.firestore}
      slug={location.pathname.split("/")?.[1]}
    ></Seo>
  );
};

export default Web3SmartContractDevelopment;

export const queryData = graphql`
  query MyQuery {
    web3SmartContractImg: file(
      relativePath: { eq: "web3-smart-contract.png" }
    ) {
      childImageSharp {
        gatsbyImageData
      }
    }
    site {
      siteMetadata {
        firestore {
          apiKey
          appId
          authDomain
          messagingSenderId
          projectId
          storageBucket
        }
      }
    }
  }
`;
